оночника является боль, чувство онемения,\n  усиление боли при физической нагрузке, поднятии тяжестей.  Другие симптомы\n  остеохондроза будут зависеть от локализации поражения (шейный, грудной,\n  поясничный отделы позвоночника).")]),_vm._v(" "),_c('h3',[_vm._v("Остеохондроз шейного отдела позвоночника")]),_vm._v(" "),_c('p',[_vm._v("Симптомами при остеохондрозе шейного отдела позвоночника являются головная\n  боль, головокружение, онемение и болевые ощущения в руках и плечах.")]),_vm._v(" "),_c('p',[_vm._v("Как правило, болевые ощущения возникают в затылке и распространяются\n  в теменно-височную область. Обычно боль постоянная и усиливается при\n  движениях шеи, чаще утром. Нередко у больных развивается синдром позвоночной\n  артерии, который складывается из жалоб на головокружение, мелькание «мушек»\n  и цветных пятен перед глазами в сочетании с жгучей пульсирующей головной болью.")]),_vm._v(" "),_c('h3',[_vm._v("Остеохондроз грудного отдела позвоночника")]),_vm._v(" "),_c('p',[_vm._v("Симптомы остеохондроза грудного отдела позвоночника определяются локализацией\n  поражения и степенью его выраженности. Основной признак — боль в грудном отделе\n  позвоночника. После физических нагрузок или долгого пребывания в одной позе\n  болевые ощущения усиливаются, что заставляет пациентов менять положение тела\n  даже ночью. Грудной остеохондроз может маскироваться под другие заболевания –\n  ишемическую болезнь сердца, гастрит, холецистит.")]),_vm._v(" "),_c('h3',[_vm._v("Остеохондроз поясничного отдела позвоночника")]),_vm._v(" "),_c('p',[_vm._v("Болевые ощущения при остеохондрозе пояснично-крестцового отдела позвоночника\n  носят ноющий характер и усиливаются во время резких движений - при попытке поднять\n  тяжести, тряской езде, чихании, кашле, изменении положения тела, а также при\n  длительном пребывании в одной позе. В горизонтальном положении болевые ощущения\n  становятся меньше. Иногда приносит облегчение вынужденная поза: лежа на спине или\n  согнувшись на здоровом боку, сидя на корточках. Нередко боль иррадиирует в ноги,\n  крестец, органы малого таза. К частыми признаками поясничного остеохондроза\n  относятся онемение, нарушение чувствительности в зонах боли. Они имеют вид\n  полос, которые захватывают ягодичную область, эти зоны идут вдоль бедра, голени,\n  реже - стопы. Нередко возникают ощущения «беганья» мурашек, покалывания, отмечаются\n  нарушение потоотделения, шелушение и сухость кожи.")]),_vm._v(" "),_c('p',[_vm._v("Методы лечения остеохондроза:")]),_vm._v(" "),_c('ol',{staticClass:"rectangle-list"},[_c('li',[_vm._v("физиотерапия")]),_vm._v(" "),_c('li',[_vm._v("иглорефлексотерапия")]),_vm._v(" "),_c('li',[_vm._v("декомпрессионное вытяжение")]),_vm._v(" "),_c('li',[_vm._v("мануальная терапия и остеопатия")])]),_vm._v(" "),_c('p',[_vm._v("Не занимайтесь самолечением! Остеохондроз сложное и грозное заболевание.\n  При первых проявлениях заболевания обратитесь к врачу. В нашей клинике Вы можете\n  получить полную консультацию о состоянии Вашего здоровья и методах лечения,\n  подходящего именно Вам.")]),_vm._v(" "),_c('p',[_vm._v("Точное количество процедур, а также сами процедуры определяются индивидуально\n  для каждого пациента лечащим врачом в зависимости от степени и типа заболевания.")])])}]
