лению – проводя манипуляции, опытный\n  врач, чувствует токи, пульсации и биения в организме пациента, что позволяет ему\n  успешно бороться с болезнью. Такая высокая чувствительность опытного остеопата\n  порой превосходит многие современные методики.")]),_vm._v(" "),_c('p',[_vm._v("Медицинские специалисты клиники остеопатии «Ноосфера» способны диагностировать\n  болезнь на ранней стадии. Это позволяет назначить оптимальный курс лечения и\n  сделать процесс более эффективным и быстрым. Как правило, даже самая запущенная\n  болезнь начинает отступать уже после первых сеансов. К окончанию курса пациент\n  возвращается к прежней жизни, забыв о прошлых мучениях и болях.")])])}]
